.recommendations {
  padding: 140px 0px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 600;

  .heading-container {
    width: 80%;
    margin-bottom: 46px;

    h2:first-of-type {
      margin-bottom: -5px;
    }

    h2 {
      font-family: livvic-bold;
      font-size: 2.7em;
      font-weight: 700;

      span {
        color: #79c8c7;
      }
    }
  }

  .recommendations-container {
    display: flex;
    gap: 30px;

    article {
      max-width: 350px;
    }

    .quotes {
      width: 67px;
      margin-bottom: -24px;
    }

    p {
      font-size: 15px;
      line-height: 22px;
      margin-bottom: 19px;
    }

    h3 {
      color: #79c8c7;
      font-family: livvic-bold;
      font-size: 18px;
      font-weight: 700;
      line-height: 28px;
    }

    h4 {
      font-family: livvic-medium-italic;
      font-size: 13px;
      font-weight: 500;
      line-height: 18px;
      margin-bottom: 25px;
    }

    .profile-pic {
      width: 62px;
      border-radius: 50%;
      border: 2px solid #c4fffe;
    }
  }
}
