.product-desc {
  background-color: #012f34;
  padding: 140px 0px;
  display: flex;
  justify-content: center;

  .container {
    display: flex;
    justify-content: space-between;
  }

  .heading-container {
    display: flex;
    flex-direction: column;
    width: 50%;

    ::before {
      content: '';
      display: block;
      background-color: #f67e7e;
      height: 4px;
      width: 50px;
      margin-bottom: 54px;
    }

    h2 {
      font-family: livvic-bold;
      font-size: 3em;
      font-weight: 700;
      letter-spacing: normal;
      text-align: left;
    }
  }

  .articles-container {
    margin-top: 54px;

    article {
      display: flex;
      margin-bottom: 30px;
    }

    h3 {
      margin-bottom: 20px;
      color: #f67e7e;
    }

    p {
      color: #ccd5d6;
      opacity: 0.8;
      font-size: 15px;
      font-weight: 600;
      line-height: 25px;
      text-align: left;
    }

    .img-container {
      width: 56px;
      margin-inline-end: 20px;

      .img {
      }
    }
  }
}
