.home-hero {
  padding-top: 77px;
  // padding: 77px 165px 0;
  font-weight: 600;
  font-size: 1.12em;
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: center;

  .main-nav {
    display: flex;
    align-items: center;
    margin-bottom: 133px;
    width: 100%;

    .main-logo {
      font-family: livvic-bold;
      font-size: 2.3em;
      letter-spacing: 1px;
      display: flex;
      align-items: center;
    }

    ul {
      display: flex;
      gap: 40px;
      align-items: center;
    }

    .contact {
      margin-inline-start: auto;
      background-color: transparent;
      color: white;
      width: 153px;
      height: 48px;
      border-radius: 24px;
      border: 2px solid #ffffff;
    }
  }

  .hero-content {
    display: flex;
    margin-bottom: 102.5px;
    gap: 100px;
    max-height: 200px;
    width: 100%;

    h1 {
      font-family: livvic-bold;
      font-size: 5.3em;
      font-weight: 700;

      .talent {
        color: #e68481;
        margin-inline-start: 20px;
      }
    }

    .hero-description {
      text-align: left;
      letter-spacing: normal;
      line-height: 28px;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      max-width: 445px;

      &::before {
        content: '';
        display: block;
        height: 5px;
        width: 50px;
        background-color: #79c7c6;
      }

      p {
        justify-self: flex-end;
        margin-top: auto;
      }

      .rectangle {
        background-color: #79c7c6;
        height: 4px;
        width: 50px;
      }
    }
  }

  .bg-pattern {
    width: 300px;
    align-self: flex-end;
  }

  .bg-pattern-1 {
    position: absolute;
    top: 50%;
    left: -100px;
    width: 200px;
    translate: 0 -20%;
  }
}
